html{
    background-color: #000000;
    --pink: #ffb1e0;
    --pink-two: #ff75c8;

    --font: 'IBM Plex Mono', monospace;
    --font-one:'Roboto Mono', monospace;
    --font-two:'Roboto', sans-serif;
}

*{
    margin: 0;
}

.App{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    color: var(--pink);
    font-family: var(--font);
}

.App a{
    text-decoration: none;
    color: var(--pink-two);
}

.text{
    width: 40vw;
    text-align: center;
    margin-top: 0;
}

::-webkit-scrollbar{
    display: none;
}
